<template>
  <div class="list">
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column prop="index" label="序号" width="35">
      </el-table-column>
      <el-table-column prop="stationName" label="变电站名称" width="80">
      </el-table-column>
      <el-table-column prop="equipName" label="设备名称" width="140">
      </el-table-column>
      <el-table-column prop="equipA" label="设备主人A角" width="65">
      </el-table-column>
      <el-table-column prop="equipB" label="设备主人B角" width="65">
      </el-table-column>
      <el-table-column prop="gourp" label="所在班组" width="80">
      </el-table-column>
      <el-table-column prop="content" label="设备范围">
      </el-table-column>
    </el-table>
  </div>
</template>

<style scoped>
  
</style>

<script>
  export default {
    data() {
      return {
        tableData: [],
        userInfo: {}
      }
    },
    created() {
      this.userInfo = JSON.parse(localStorage.getItem('userInfo'))
      this.initData()
    },
    methods: {
      initData() {
        let params
        if (this.$route.query.type === 'user') {
          params = {
            uid: this.userInfo.uid
          }
        } else {
          params = {
            uid: ''
          }
        }
        this.$http.get('', {params: params})
        .then((res) => {
          if (res.code === 200) {
            this.tableData = res.data
          } else {
            alert(res.msg)
          }
        })
        .catch((err) => {
          alert(err)
        })
      }
    },
    components: {
  
    }
  }
</script>

